
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'

import api from '../api/api'
import auth from '../api/auth'
import env from '../config/env'
export default {
    data() {
        return {
            // 从服务端获取的验证码
            verifyCode: '',
            img_url: null,
            phone: '',
            // 用户输入的验证码
            inputVerifyCode: '',
            showLoading: false
        }
    },
    mounted() {
        this.getVerifyCode()
    },
    methods: {
        // 获取验证码
        getVerifyCode() {
            let vm = this
            // 获取图片验证码
            api.get('getWeChatVerifyCode').then((res) => {
                if(res.status===1){
                    vm.verifyCode = res.data.verifyCode
                    vm.img_url = env.serverUrl + res.data.img_url
                }else{
                    vm.$vux.alert.show({
                        title: env.alertInfo,
                        content: res.message,
                        buttonText: '确定',
                        dialogTransition:'',
                        maskTransition:''
                    })
                }
            }).catch(error => {
                vm.$vux.alert.show({
                    title: env.alertInfo,
                    content: env.errorInfo,
                    buttonText: '确定',
                    dialogTransition:'',
                    maskTransition:''
                })
            })
        },
        // 登录
        login() {
            if (!this.phone) {
                this.$vux.alert.show({
                    title: env.alertInfo,
                    content: '请输入手机号',
                    buttonText: '确定',
                    dialogTransition:'',
                    maskTransition:''
                })
                return
            }
            if (!this.inputVerifyCode) {
                this.$vux.alert.show({
                    title: env.alertInfo,
                    content: '请输入验证码',
                    buttonText: '确定',
                    dialogTransition:'',
                    maskTransition:''
                })
                return
            }
            if (this.inputVerifyCode.toLowerCase() != this.verifyCode.toLowerCase()) {
                this.$vux.alert.show({
                    title: env.alertInfo,
                    content: '验证码输入有误',
                    buttonText: '确定',
                    dialogTransition:'',
                    maskTransition:''
                })
                return
            }
            // 执行登录
            auth.login(this.phone, () => {
                    // 登录成功，跳转至之前访问的页面
                    this.$router.replace(this.$route.query.redirect || '/')
                }, 
                () => {
                    // 登录失败
                    this.$vux.alert.show({
                        title: '登录失败',
                        content: '手机号错误',
                        buttonText: '确定',
                        dialogTransition:'',
                        maskTransition:''
                    })
                    // 刷新验证码
                    this.getVerifyCode()
                })
        }
    },
    components: {
        XHeader, 
        XInput, 
        XButton, 
        Group
    }
}



